.page6{
    height: 100vh;
    font-family: 'Poppins';
}
.page6header{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10%;
    font-size: 65px;
    font-weight: 500;
    color: rgba(97, 122, 85, 1);
    text-align: center;
}
.underheadpage6{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 10%;
    font-size: 25px;
    font-weight: 500;
    color:rgb(148, 180, 134) ;
    text-align: center;
    margin-top: 20px;
}
.maincontentpage6{
    height: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.page6first{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.page6second{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;

}
.page6image{
    width: 75%;
}
.insidecontentpg6{
    width: 70%;
    text-align: justify;
    font-size: 1.3rem;
    font-weight: 500;
}