.page7{
    height: 60vh;
    font-family: 'Poppins';
}
.page7header{
    font-size: 55px;
    font-weight: 500;
    color: rgba(97, 122, 85, 1);
    text-align: center;
}
.box1{
    width: 450px;
    height: 125px;
    background-color: rgba(97, 122, 85, 1);
    border-radius: 20px;
    margin: 10px;
    display: flex;
    justify-content: center;
align-items: center;
}
.boxdiv{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.insideboxdiv{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70%;
    flex-wrap: wrap;
}
.image{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 100%;
}
.boximg{
    width: 65%;
}
.taglinepg7{
width: 70%;
height: 100%;
display: flex;justify-content: center;align-items: center;
font-size: 1.2rem;
font-weight: 500;
color: white;

}