.page3{
    height:80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    font-family: 'Poppins';
}
.contentpage3{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.page3firsthalf{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transform: translateX(15%);
}
.page3secondhalf{
    width: 50%;
    height:100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(-15%);
}
.headerpage3{
    font-size: 35px;
    font-weight:500;
}
.maincontentpage3{
    width: 100%;
    text-align: right;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 40px;
}
.insidecontentpage3{
    width: 65%;
    text-align: justify;
    font-size: 1.15rem;
    font-weight: 500;

}
.arrowpage3{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items:center;
    transform: translateY(-50%) translateX(0%);
    margin: 0;
}
.firstcontent{
    display: flex;
    justify-content: center;
    align-items: center;
}

@media only screen and (max-width: 800px){
    .page3{
        height: 100vh;
        
    }
}