.page2{
    height: 100vh;
    font-family: 'Poppins';
    display: flex;
    justify-content: center;
    align-items: center;
}
.page2image{
    width: 75%;
}
.page2firsthalf{
    width: 45%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.page2secondhalf{
    width: 55%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transform: translateX(15%);
}
.headerpage2{
    font-size:35px ;
    width: 100%;
    margin-bottom: 20px;
    font-weight: 600;
}
.insideheader{
    width: 65%;
}
.maincontentpage2{
    width: 100%;
    margin-top: 20px;
}
.insidecontent{
    width: 65%;
    text-align: justify;
    font-weight: 500;

}
.buttonsdivpage{
    margin-top: 20px;
    width:100%;
    height: 100px;
    transform: translateY(50%);
}
.loginbuttonpage2{
    width: 200px;
    height: 50px;
    border-radius: 40px;
    border: none;
    background-color: rgb(211, 211, 211);
    font-size: 1rem;
    font-weight: 600;
    color: rgba(97, 122, 85, 1);
    margin-left: 20px;
}
.ordernowbuttonpage2{
    width: 200px;
    height: 50px;
    border: none;
    border-radius: 40px;
    background-color:rgba(97, 122, 85, 1) ;
    color: white;
    font-weight:600;
    font-size: 1rem;
    margin-right: 20px;
}
.ordernowbuttonpage2:hover{
    border: 3px solid rgba(97, 122, 85, 1);
    background-color: white;
    color: rgba(97, 122, 85, 1);
    transition: 0.3s;
}

@media only screen and (max-width: 800px){
    .page2{
        height: 100vh;
        font-family: 'Poppins';
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        /* background-color: black; */
    }
    .page2image{
        width: 100%;
        transform: translateX(4%) ;
    }
    .page2firsthalf{
        width: 100%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .page2secondhalf{
        width: 100%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .headerpage2{
        font-size:25px ;
        width: 100%;
        margin-bottom: 0px;
        font-weight: 600;
    }
    .insideheader{
        width: 90%;
    }
    .maincontentpage2{
        width: 100%;
        margin-top: 10px;
    }
    .insidecontent{
        width:90%;
        text-align: justify;
        font-weight: 500;
    }
    .buttonsdivpage{
        margin-top: 10px;
        width:100%;
        height: 100px;
        transform: translateY(25%) translateX(-5%);
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .loginbuttonpage2{
        width: 180px;
        height: 50px;
        border-radius: 40px;
        border: none;
        background-color: rgb(211, 211, 211);
        font-size: 0.9rem;
        font-weight: 600;
        color: rgba(97, 122, 85, 1);
        margin-left: 5px;
    }
    .ordernowbuttonpage2{
        width: 180px;
        height: 50px;
        border: none;
        border-radius: 40px;
        background-color:rgba(97, 122, 85, 1) ;
        color: white;
        font-weight:600;
        font-size: 1rem;
        margin-right: 5px;
    }
    .ordernowbuttonpage2:hover{
        border: 3px solid rgba(97, 122, 85, 1);
        background-color: white;
        color: rgba(97, 122, 85, 1);
        transition: 0.3s;
    }
    
}