html{
    overflow-x: hidden;
}
.headermain{
    position: sticky;
    z-index: 200;
    top: 0;
    background-color: white;
    width: 100%;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    overflow-x: hidden;
}
.logoheader{
    width: 10%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.headerlinks{
    width: 30%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Poppins';
    transform: translateX(-35%);
}
.linkslink{
    font-size: 16px;
    text-decoration: none;
    color: black;
    font-weight: 500;
    padding: 20px;
    font-family: 'Poppins';
}
.linkslink:hover{
    background-color: rgba(97, 122, 85, 1);
    border-radius: 40px;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;
    color: white;
    transition: 0.3s;
}
.buttondiv{
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins';
    transform: translateX(10%);
}
.Menubutton{
    font-size: 17px;
    font-weight: 600;
    letter-spacing: 2px;
    width: 180px;
    height: 50px;
    border: none;
    border-radius: 40px;
    background-color:rgba(97, 122, 85, 1) ;
    color: white;
    margin-left: 20px;
    margin-right: 20px;
    font-family: 'Poppins';
}
.Menubutton:hover{
    background-color: white;
    border: 3px solid rgba(97, 122, 85, 1);
    transition: 0.3s;
    color: rgba(97, 122, 85, 1);
    font-family: 'Poppins';
}
.loginbutton{
    background-color:rgb(210, 210, 210) ;
    color: rgba(97, 122, 85, 1);
}
.checkboxheader{
    display: none;
}

@media only screen and (max-width: 800px){
    
    .headermain{
        background-color: white;
        z-index: 100;
        width: 100%;
        height: 100%;
        position: fixed;
        left: -100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border-right: 5px solid green;
        transition: 0.5s;
    }
    .headerlinks{
        width: 30%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: 'Poppins';
        /* transform: translateX(-35%); */
    }
    .logoheader{
        width: 10%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        scale: 0.8;
        transform: translateX(-450%) translateY(-40%);
    }
    .checkboxheader{
        position: fixed;
        right:5%;
        top: 2%;
        display: block;
        z-index: 100;
    }
    .haminp{
        width: 60px;
        height: 60px;
        background-color: rgba(97, 122, 85, 1);
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .buttondiv{
       flex-direction: column;
       transform: translateX(-4%);
    }
    .Menubutton{
        margin-top: 15px;
        margin-bottom: 15px;
    }
    .headerlinks{
       flex-direction: column;
       width: 100%;
       display: flex;
       justify-content: center;
       align-items: center;
    }
    .linkslink{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        transform: translateX(30%) translateY(-100%);
        font-size: 1.5rem;
    }
    .hamburger {
        cursor: pointer;
    }
    
    .hamburger input {
      display: none;
    }
    
    .hamburger svg {
      /* The size of the SVG defines the overall size */
      height: 3em;
      /* Define the transition for transforming the SVG */
      transition: transform 600ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .line {
      fill: none;
      stroke: rgb(0, 0, 0);
      stroke-linecap: round;
      stroke-linejoin: round;
      stroke-width: 3;
      /* Define the transition for transforming the Stroke */
      transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
                  stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
    }
    
    .line-top-bottom {
      stroke-dasharray: 12 63;
    }
    
    .hamburger input:checked + svg {
      transform: rotate(-45deg);
    }
    
    .hamburger input:checked + svg .line-top-bottom {
      stroke-dasharray: 20 300;
      stroke-dashoffset: -32.42;
    }
    .headerlinkmobile{
      display: block;
      background-color: rgba(97, 122, 85, 1);
      border-radius: 50%;
      width: 70px;
      height: 70px;
    }
      
}