.page4{
    height: 80vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins';
    flex-direction: column;
}
.page4firsthalf{
    width: 45%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateX(10%) translateY(0%);
}
.arrowimage
{
    height: 1%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translateY(-2500%) translateX(-50%);
}
.page4arrow{
    width: 150px;
}
.page4secondhalf{
    width: 45%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: left;
}
.page4header{
    font-size: 35px;
    font-weight: 500;
    color: rgba(97, 122, 85, 1);
    width: 75%;
    margin-bottom: 20px;
}
.page4content{
    margin-top: 20px;
    width: 75%;
    text-align: justify;
    font-size: 1.15rem;
    font-weight: 500;
}
.pg4content{
    display: flex;
    justify-content: center;
    align-items: center;
}

