
.page1{
    height: 90vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Poppins';
}
.page1firsthalf{
    width: 45%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transform: translateX(10%);
}
.firsttextpage1{
    font-size: 80px;
    font-weight: 600;
transform: translateX(05%) translateY(-15%);
}
.greentext{
    color:rgba(97, 122, 85, 1) ;
}
.undertextfirst{
    color: grey;
    font-size: 1.2rem;
    transform: translateY(-200%) translateX(2%);
}
.ordernowbutton{
    width: 200px;
    height: 50px;
    border: none;
    border-radius: 40px;
    background-color:rgba(97, 122, 85, 1) ;
    color: white;
    font-weight:600;
    font-size: 1rem;
    transform: translateY(-10%) translateX(-100%);
}
.ordernowbutton:hover{
    border: 3px solid rgba(97, 122, 85, 1);
    background-color: white;
    color: rgba(97, 122, 85, 1);
    transition: 0.3s;
}

.secondhalf{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.page4image{
    width: 75%;
}

@media only screen and (max-width: 800px){
    .page1{
        height: 120vh;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column-reverse;
        font-family: 'Poppins';
    }
    .page1firsthalf{
        width: 100%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        transform: translateX(0%);
    }
    .firsttextpage1{
        font-size: 40px;
        font-weight: 600;
        transform: translateX(13%) translateY(-20%);
    }
    .page4image{
        width: 100%;
        transform: translateX(4%) translateY(10%);
    }
    
    .greentext{
        color:rgba(97, 122, 85, 1) ;
    }
    .undertextfirst{
        color: grey;
        font-size: 1.2rem;
        width: 80%;
        transform: translateY(-30%) translateX(5%);
    }
    .ordernowbutton{
        width: 200px;
        height: 50px;
        border: none;
        border-radius: 40px;
        background-color:rgba(97, 122, 85, 1) ;
        color: white;
        font-weight:600;
        font-size: 1rem;
        transform: translateY(-0%) translateX(-25%);
    }
    .ordernowbutton:hover{
        border: 3px solid rgba(97, 122, 85, 1);
        background-color: white;
        color: rgba(97, 122, 85, 1);
        transition: 0.3s;
    }
    .secondhalf{
        width: 100%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
}